/* CSS for entire Hero component */
.hero-wrapper {
    padding-top: 7rem;
}

@media screen and (max-width: 1023px) {
    .hero-wrapper {
        padding-top: 5rem;
    }
}

/*CSS for Intro component */
.intro-group {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2vw;
}

.hello-group {
    position: relative;
    margin-bottom: 1vw;
}

.hello-text {
    border-style: solid;
    border-radius: 30px;
    width: fit-content;
    padding: 5px 15px;
    font-size: 1.25rem;
}

.hello-text {
    border-style: solid;
    border-radius: 30px;
    width: fit-content;
    padding: 5px 15px;
    font-size: 1rem;
}

.hello-vector {
    position: absolute;
    bottom: 1.25rem;
    left: 4.25rem;
    width: 2.75rem;
    height: auto;
}

.name-group {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.name-group p {
    font-size: 4rem;
    font-family: var(--title-font);
    font-weight: 600;
    color: black;
}

.name-text {
    display: flex;
}

.name-vector {
    width: 4vw;
    height: auto;
    transform: rotate(35deg);
    margin-right: 0.5vw;
}

#name-text-occupation {
    font-family: var(--title-font);
    font-weight: 600;
    color: black;
    display: flex;
    font-size: 4rem;
    padding-right: 3vw;
    margin-top: -1vw;
}

/*CSS for Intro component - animated occupation section */
#animated-heading {
    opacity: 0;
    -webkit-transition: opacity 0.3s ease-in-out;
    -moz-transition: opacity 0.3s ease-in-out;
    -ms-transition: opacity 0.3s ease-in-out;
    -o-transition: opacity 0.3s ease-in-out;
    transition: opacity 0.3s ease-in-out;
}

/*CSS for IconBar component */
#icon-bar-text-title {
    font-weight: 600;
    font-size: 2.25rem;
    font-family: var(--title-font);
}

.icon-bar {
    display: flex;
    flex-direction: column;
    margin: 6vw 20% 2vw;
    z-index: 3;
}

.icon-bar-shifted {
    position: relative;
    bottom: 19vw;
}

.icon-bar-icons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
}

.icon-bar-icons img {
    width: 2.5rem;
    height: auto;
    margin-bottom: 1rem;
}

.icon-bar p {
    color: black;
    font-family: var(--body-font);
    font-size: 1rem;
}

/*CSS for PhotoArea component */
.photo-area {
    display: flex;
    position: relative;
    align-items: flex-end;
    justify-content: center;
    padding-block-start: 20%;
}

.photo-area img {
    position: absolute;
}

#photoLien {
    width: 30%;
    max-width: 30%;
    bottom: 5%;
    z-index: 3;
}

#vectorEclipse {
    width: 60%;
    z-index: 0;
    min-width: none;
}

/* CSS for PortfolioButton */
.portfolio-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: -6rem;
    z-index: 5;
}

.portfolio-group {
    border: solid;
    border-radius: 70px;
    display: flex;
    flex-direction: row;
    backdrop-filter: blur(7.5px);
    width: fit-content;
    position: relative;
    padding: 0.5rem;
}

.portfolio-button-active {
    display: flex;
    flex-direction: row;
    padding: 0.5rem 2rem;
    align-items: center;
    border-radius: 70px;
    border-color: #2685bf;
    background-color: #2685bf;
    border-style: none;
    transition: padding-left 0.3s ease-in-out;
    cursor: pointer;
}

.portfolio-button-active img {
    width: 20px;
    margin-left: 10px;
}

.portfolio-button-active p {
    color: white;
    font: var(--body-font);
    font-size: 1.25rem;
    display: flex;
    align-items: center;
}

.portfolio-text {
    color: white;
    font: var(--body-font);
    font-size: 1.25rem;
    display: flex;
    align-items: center;
    padding: 0 1rem 0 1rem;
    background-color: transparent;
    border-width: 0;
}

/* CSS for BackgroundVectors */
.vector-wrapper-inactive {
    display: none;
}

.vector-wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    top: -27rem;
    z-index: 1;
}

.vector-wrapper img {
    position: absolute;
}

